import {Component, Input, OnInit} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {Router} from "@angular/router";
import {UserServiceService} from "../user-service.service";

@Component({
  selector: 'app-users-list',
  templateUrl: './users-list.component.html',
  styleUrls: ['./users-list.component.css']
})
export class UsersListComponent implements OnInit {

  constructor(private http : HttpClient, private router : Router, private userService : UserServiceService ) { }
@Input() users : any;
  filter : string;


  ngOnInit() {
   this.retrieveUsers();
  }

  retrieveUsers(){
    this.filter='';
    this.searchUsers();
  }

  searchUsers() {///wordgame/api/admins/v3/:uid
    var THINGS_URL = `http://localhost:3000/wordgame/api/admins/v3/user`;
    this.http.get( THINGS_URL ).subscribe(
      data => {
        this.users = data;
      }
    );
    this.filter = '';
  }
  viewUser(type){
    if(type=="create"){
      this.router.navigate(['useritem']);
    }else{

    }
  }
  logout() {
    var LOGOUT_URL = "http://localhost:3000/wordgame/api/logout/v3";
    this.http.post( LOGOUT_URL, {} ).subscribe(
      data => {
        this.router.navigate(['login']);
      }
    )
  }
}



